<template>
    <div class="record">
    <p class="nameBox"> <el-input v-model="nameInput" placeholder="" class="input" ></el-input><el-button :plain="true" @click="nameFound"></el-button>
           <el-input v-model="numberInput" placeholder="" class="input"></el-input><el-button :plain="true" @click="numberFound"></el-button> </p>
        
    <el-table
    :data="workerDatas"  style="width: 100%">
    <el-table-column     prop="Name"    label=""    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label=""   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label=""  width="800">
      <template slot-scope="scope">
        <el-button @click="dialog=true;handleClick(scope.row)" type="text" size="small"></el-button>
                    <el-dialog title="" :visible.sync="dialog" :modal="false">
                            <el-form :model="form">
                                <el-form-item label=":" :label-width="formLabelWidth">
                                    <p>{{form.Name}}</p>   
                                </el-form-item>
                                <el-form-item label=":" :label-width="formLabelWidth">
                                <p>{{form.Number}}</p>
                                </el-form-item>
                                <el-form-item label="" :label-width="formLabelWidth">
                                <el-select v-model="form.some" placeholder="">
                                    <el-option label="" value=""></el-option>
                                    <el-option label="" value=""></el-option>
                                    <el-option label="" value=""></el-option>
                                </el-select>
                                </el-form-item>
                                <el-form-item label=":" :label-width="formLabelWidth">
                                         <el-input v-model="timeYear" class="inputYear" autocomplete="off" type="number" min="2000" max="2030"></el-input>
                                         <el-input v-model="timeMouth" class="inputtime" autocomplete="off" type="number" min="1" max="12"></el-input>
                                         <el-input v-model="timeDay" class="inputtime" autocomplete="off" type="number" min="1" max="31"></el-input>
                                </el-form-item> 
                               
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialog = false"> </el-button>
                                <el-button type="primary" @click="changeSure"> </el-button>
                            </div>
                            </el-dialog>
      </template>
    </el-table-column>
  </el-table>
 

    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
export default {
    data(){
        return{
        dialog: false,
        workerDatas:[],
        timeYear:'',
        timeMouth:'',
        id:0,
        timeDay:'',
        formLabelWidth: '120px',
        nameInput:'',
        numberInput:'',
        form: {
                    Name: '',
                    Number:'',
                    some:'',
              },
        }
    }, created:function(){                                     //created dom  
                 axios.get(this.HOST+'/worker/getRecordList')            //json
                .then(reponse=>{
                    let getRecordList=reponse.data.data            //jsondataworkerDatas
                    let List = getRecordList
                    let number = []             
                    for(let i=0;i<List.length;i++){
                       number[i]=List[i].id                         //dataNumber
                   }
                   let newNumber =  parseInt(number[number.length-1])
                   this.id=newNumber+1  
                    console.log(this.id)
                   
               })
                let time = new Date();
                 let timeStr =String(time)
                let timeCut = timeStr.split(' ')
                //console.log(timeCut)
                 this.timeYear = parseInt(timeCut[3])
                 this.timeDay = parseInt(timeCut[2])
                 let timeMouth = timeCut[1]

                 if(timeMouth=='January'){
                     this.timeMouth= 1
                 }else if(timeMouth=='February'){
                     this.timeMouth=2
                 }else if(timeMouth=='March'){
                     this.timeMouth=3
                 }else if(timeMouth=='April'){
                     this.timeMouth=4
                 }else if(timeMouth=='May'){
                     this.timeMouth=5
                 }else if(timeMouth=='June'){
                     this.timeMouth=6
                 }else if(timeMouth=='July'){
                     this.timeMouth=7
                 }else if(timeMouth=='August'){
                     this.timeMouth=8
                 }else if(timeMouth=='September'){
                     this.timeMouth=9
                 }else if(timeMouth=='October'){
                     this.timeMouth=10
                 }else if(timeMouth=='November'){
                     this.timeMouth=11
                 }else if(timeMouth=='December'){
                     this.timeMouth=12
                 }
               
            },


    methods:{

         handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   
         handleClick(row) {
       // console.log(row);
      },


        nameFound:function(){
                   let nameInputResult=this.nameInput;            //
                   let reg=/^[\u4e00-\u9fa5]+$/;              //
                    if(!reg.test(nameInputResult)){
                          this.$message({
                            showClose: true,
                            message: '',
                            type: 'error'
                        });
                           
                       }else{

                            axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrName':this.nameInput,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundName',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.nameInput=''
                                    //console.log(res)
                                    if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                          type: 'error'
                                          });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                         }
                },


                //
                numberFound:function(){
                let numberInputResult = parseInt(this.numberInput);                    //              
                   let rem=/^\d{7}$/;                                         //
                    if(!rem.test(numberInputResult)){
                      
                           this.$message({
                            showClose: true,
                            message: '',
                            type: 'error'
                        });
                        //alert(
                          // alert("")
                       }else{
                            

                           axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workerNumber':numberInputResult,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundNumber',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.numberInput=''
                                   if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                       });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                    }

                    },
                    //
                handleClick(id) {
                    let foundNumber = id.Number            // 
                    let number=this.workerDatas                     
                    let numbers = []       
                    for(let i=0;i<number.length;i++){
                        numbers[i]=number[i].Number              //Number
                    }
                    let result = numbers.indexOf(foundNumber)        //
                    if(result!=-1){
                        this.form = this.workerDatas[result]          //form
                    }     
                    },

                changeSure:function(){
                   //console.log('1')
                   if(this.form.some==null){
                       this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                        });
                   }else{
                                 
                        axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'Name': this.form.Name,
                                    'Number':this.form.Number,
                                    'some':this.form.some,
                                    'id':this.id,
                                    'timeYear':this.timeYear,
                                     'timeMouth':this.timeMouth,
                                     "timeDay":this.timeDay
                                });       
                                axios({
                                        url:this.HOST+'/worker/addRecord',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                  .then((res)=>{
                                     
                                     // console.log(res)
                                     if(res.data.message==""){
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                        });
                                        this.dialog = false
            
                                    }else if (res.data.message=="") {
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'success'
                                        });
                                      this.dialog = false
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                     
                            }
                 }

          },
   
}
</script>
<style scoped>
 .input{
    width: 200px;
}   
 .inputtime{
    width: 70px;
}   
 .inputYear{
    width: 80px;
}  
.nameBox{
    text-align: center;
} 
el-dialog{
    text-align: center;
}
</style>


