<template>
        <div>
         <el-table
    :data="workerDatas.slice((currentPage-1)*pageSize,currentPage*pageSize)"  style="width: 100%">
    <el-table-column     prop="Name"    label=""    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label=""   width="120">
    </el-table-column>
    <el-table-column   prop="Record"   label=""   width="120">
    </el-table-column>
    <el-table-column   prop="time"   label=""   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label=""  width="650">
        <template slot-scope="scope">
                 <el-button @click.native.prevent="deleteList(scope.row,scope.$index,workerDatas)"type="text" size="small"></el-button>
                  </template>
    </el-table-column>
  </el-table>
 <div class="block" style="margin-top:15px;">
             <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10]"  layout="total, sizes, prev, pager, next, jumper" :total="workerDatas.length">
               </el-pagination>
           </div>  
   
    </div>
</template>

<script>

import axios from 'axios';
import qs from 'qs';


export default {
    data(){
        return{
        workerDatas:[],
        currentPage: 1, // 
        total: 20, // 
        pageSize: 10, // 
        formLabelWidth: '120px',
        }
    }, created:function(){                                     //created dom  
                axios.get(this.HOST+'/worker/getaddWork')            //json
                .then(reponse=>{
                    this.workerDatas=reponse.data.data            //jsondataworkerDatas                
                })
            },


    methods:{
        handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   
             //
             deleteList:function(id,index,rows){
                 this.$confirm(', ?', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                            }).then(() => {
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'found': id.id,
                                });       
                                axios({
                                        url:this.HOST+'/worker/deleteRecord',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    rows.splice(index, 1);
                                     this.$router.go(0)
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                       }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });          
                            });    
                 },          
    }
}
</script>
<style scoped>
    
</style>


