<template>
    <div class="record">
    <p class="nameBox"> <el-input v-model="nameInput" placeholder="" class="input" ></el-input><el-button :plain="true" @click="nameFound"></el-button>
           <el-input v-model="numberInput" placeholder="" class="input"></el-input><el-button :plain="true" @click="numberFound"></el-button> </p>
        
    <el-table
    :data="workerDatas"  style="width: 100%">
    <el-table-column     prop="Name"    label=""    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label=""   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label=""  width="800">
      <template slot-scope="scope">
        <el-button @click="dialog=true;handleClick(scope.row)" type="text" size="small"></el-button>
                    <el-dialog title="" :visible.sync="dialog" :modal="false">
                            <el-form :model="form">
                                <el-form-item label=":" :label-width="formLabelWidth">
                                    <p>{{Record}}</p>   
                                </el-form-item>
                                <el-form-item label=":" :label-width="formLabelWidth">
                                    <p>{{form.Name}}</p>   
                                </el-form-item>
                                <el-form-item label=":" :label-width="formLabelWidth">
                                <p>{{form.Number}}</p>
                                </el-form-item>
                                <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.Money" type="number" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                 <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.things" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label=":" :label-width="formLabelWidth">
                                         <el-input v-model="timeYear" class="inputYear" autocomplete="off" type="number" min="2000" max="2030"></el-input>
                                         <el-input v-model="timeMouth" class="inputtime" autocomplete="off" type="number" min="1" max="12"></el-input>
                                         <el-input v-model="timeDay" class="inputtime" autocomplete="off" type="number" min="1" max="31"></el-input>
                                </el-form-item> 
                               
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialog = false"> </el-button>
                                <el-button type="primary" @click="changeSure"> </el-button>
                            </div>
                            </el-dialog>
      </template>
    </el-table-column>
  </el-table>
<br><br>
<p class="history"></p>
<br>
  <el-table
    :data="lookDatas.slice((currentPage-1)*pageSize,currentPage*pageSize)"  style="width: 100%">
    <el-table-column     prop="Record"    label=""    width="140">
    </el-table-column>
    <el-table-column     prop="Name"    label=""    width="140">
    </el-table-column>
    <el-table-column   prop="Number"   label=""   width="140">
    </el-table-column>
    <el-table-column   prop="things"   label=""   width="150">
    </el-table-column>
    <el-table-column   prop="money"   label=""   width="140">
    </el-table-column>
    <el-table-column   prop="time"   label=""   width="140">
    </el-table-column>
    <el-table-column    fixed="right"    label=""  width="240">
          <template slot-scope="scope">
                 <el-button @click.native.prevent="deleteList(scope.row,scope.$index,workerDatas)"type="text" size="small"></el-button>
                  </template>
    </el-table-column>
  </el-table>
 <div class="block" style="margin-top:15px;">
             <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10]"  layout="total, sizes, prev, pager, next, jumper" :total="workerDatas.length">
               </el-pagination>
           </div>  
 

    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
export default {
    data(){
        return{
        dialog: false,
        workerDatas:[],
        timeYear:0,
        timeMouth:0,
        timeDay:0,
        Record:0,
        formLabelWidth: '120px',
        nameInput:'',
        numberInput:'',
        lookDatas:[],
        currentPage: 1, // 
        total: 20, // 
        pageSize: 10, // 
        form: {
                    Name: '',
                    Number:'',
                    Money:0,
                    things:'',


              },
        }
    },created:function(){                                     //created dom  
                         //jsondataworkerDatas
                //console.log(this.workerDatas)
                 axios.get(this.HOST+'/worker/getMoneyList')            //json
                .then(reponse=>{
                  
                   this.lookDatas=reponse.data.data            //jsondataworkerDatas
                    let List = this.lookDatas
                    let number = []             
                    for(let i=0;i<List.length;i++){
                       number[i]=List[i].Record                         //dataNumber
                   }
                   let newNumber =  parseInt(number[number.length-1])
                   this.Record=newNumber+1  

               })
                let time = new Date();
                 let timeStr =String(time)
                let timeCut = timeStr.split(' ')
                //console.log(timeCut)
                 this.timeYear = parseInt(timeCut[3])
                 this.timeDay = parseInt(timeCut[2])
                 let timeMouth = timeCut[1]

                 if(timeMouth=='January'){
                     this.timeMouth= 1
                 }else if(timeMouth=='February'){
                     this.timeMouth=2
                 }else if(timeMouth=='March'){
                     this.timeMouth=3
                 }else if(timeMouth=='April'){
                     this.timeMouth=4
                 }else if(timeMouth=='May'){
                     this.timeMouth=5
                 }else if(timeMouth=='June'){
                     this.timeMouth=6
                 }else if(timeMouth=='July'){
                     this.timeMouth=7
                 }else if(timeMouth=='August'){
                     this.timeMouth=8
                 }else if(timeMouth=='September'){
                     this.timeMouth=9
                 }else if(timeMouth=='October'){
                     this.timeMouth=10
                 }else if(timeMouth=='November'){
                     this.timeMouth=11
                 }else if(timeMouth=='December'){
                     this.timeMouth=12
                 }
               
            },


    methods:{

         handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   
         handleClick(row) {
       // console.log(row);
      },


        nameFound:function(){
                   let nameInputResult=this.nameInput;            //
                   let reg=/^[\u4e00-\u9fa5]+$/;              //
                    if(!reg.test(nameInputResult)){
                          this.$message({
                            showClose: true,
                            message: '',
                            type: 'error'
                        });
                           
                       }else{

                            axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrName':this.nameInput,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundName',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.nameInput=''
                                    //console.log(res)
                                    if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                     });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                         }
                },


                //
                numberFound:function(){
                let numberInputResult = parseInt(this.numberInput);                    //              
                   let rem=/^\d{7}$/;                                         //
                    if(!rem.test(numberInputResult)){
                      
                           this.$message({
                            showClose: true,
                            message: '',
                            type: 'error'
                        });
                        //alert(
                          // alert("")
                       }else{
                            

                           axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workerNumber':numberInputResult,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundNumber',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    this.numberInput=''
                                   if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                       });
                                    }
                            
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                    }

                    },
                    //
                handleClick(id) {
                    let foundNumber = id.Number            // 
                    let number=this.workerDatas                     
                    let numbers = []       
                    for(let i=0;i<number.length;i++){
                        numbers[i]=number[i].Number              //Number
                    }
                    let result = numbers.indexOf(foundNumber)        //
                    if(result!=-1){
                        this.form = this.workerDatas[result]          //form
                    }     
                    },
                //
                deleteList:function(id,index,rows){
               // console.log(id.Number)
                 this.$confirm(', ?', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                            }).then(() => {
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'found': id.Record,
                                });       
                                axios({
                                        url:this.HOST+'/worker/deleteMoneyList',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    rows.splice(index, 1);
                                     this.$router.go(0)
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                       }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });          
                            });    
                 },


                //
                changeSure:function(){
                   //console.log('1')
                    let config = /^[0-9]*$/
                    let text = this.form.Money

                   if(!config.test(text)){
                       this.$message({
                                        showClose: true,
                                        message: ',',
                                        type: 'error'
                                        });
                   }else{
                        axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'Name': this.form.Name,
                                    'Number':this.form.Number,
                                    'Money':this.form.Money,
                                    'Record':this.Record,
                                    'things':this.form.things,
                                    'timeYear':this.timeYear,
                                     'timeMouth':this.timeMouth,
                                     "timeDay":this.timeDay
                                });       
                                axios({
                                        url:this.HOST+'/worker/addMoney',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                  .then((res)=>{
                                     if(res.code=1){
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'success'
                                        });
                                    this.$router.go(0)
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                     
                            }
                 }

          },
   
}
</script>
<style scoped>
 .input{
    width: 200px;
}   
 .inputtime{
    width: 70px;
}   
 .inputYear{
    width: 80px;
}   
.history{
    text-align: center;
    color: brown;
    font-size: 25px;
}
.nameBox{
    text-align: center;
}
el-dialog{
    text-align: center;
}
</style>


